// Inscription.js
import React, { useState } from 'react';
import './App.css'; // Assurez-vous de crer un fichier CSS spar pour votre style

const Inscription = ({ onClose }) => {
  const [formData, setFormData] = useState({
    nom: '',
    prenom: '',
    age: '',
    email: '',
  });

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Ajoutez ici la logique pour le traitement du formulaire (ventuellement avec l'utilisation de l'tat local)
    // Vous pouvez utiliser les donnes dans formData pour effectuer le traitement
    // ...

    // Fermez le formulaire aprs le traitement
    onClose();
  };

  return (
    <section className="registration-form">
      <h2>Inscription</h2>
      <form onSubmit={handleSubmit}>
        {/* Champ Nom */}
        <label htmlFor="nom">Nom :</label>
        <input
          type="text"
          id="nom"
          name="nom"
          value={formData.nom}
          onChange={handleChange}
          required
        />

        {/* Champ Prnom */}
        <label htmlFor="prenom">Prnom :</label>
        <input
          type="text"
          id="prenom"
          name="prenom"
          value={formData.prenom}
          onChange={handleChange}
          required
        />

        {/* Champ ge */}
        <label htmlFor="age">ge :</label>
        <input
          type="number"
          id="age"
          name="age"
          value={formData.age}
          onChange={handleChange}
          required
        />

        {/* Champ Email */}
        <label htmlFor="email">Email :</label>
        <input
          type="email"
          id="email"
          name="email"
          value={formData.email}
          onChange={handleChange}
          required
        />

        {/* Bouton de Soumission */}
        <input type="submit" value="S'inscrire" />
      </form>
    </section>
  );
};

export default Inscription;
