import React, { useState } from 'react';
import './App.css'; // Assurez-vous de crer un fichier CSS spar pour votre style
import Linkedin from './linkedin.png';
import Facebook from './facebook.png';
import Instagram from './instagram.png';
import Twitter from './twitter.png';

const Section4 = () => {
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');

    const isValidEmail = (email) => {
        // Expression rgulire pour vrifier si l'e-mail est valide
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    const handleSignIn = () => {
        if (!email.trim() || !isValidEmail(email)) {
            setMessage(<span className="email-error">Veuillez entrer une adresse e-mail valide.</span>);
            return;
        }
        // Envoyer l'adresse e-mail au backend Flask
        //fetch('http://localhost:5000/submit_form', {
        fetch('https://stratup1.azurewebsites.net/submit_form', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ email: email })
        })
        .then(response => {
            if (response.ok) {
                console.log('Adresse email envoye avec succs au backend Flask.');
                setEmail(''); // Rinitialiser le champ email aprs l'envoi russi
                // Changer le texte et la couleur du bouton pendant 5 secondes
                setMessage(<span className="email-message">Crons ensemble un avenir financier sans contraintes !</span>);
                const button = document.getElementById('submitBtn');
                button.innerText = 'Inscription russie!';
                button.style.backgroundColor = 'rgb(0, 67, 67)'; // Changer la couleur du bouton
                setTimeout(() => {
                    setMessage('');
                    button.innerText = 'Rester connect !';
                    button.style.backgroundColor = ''; // Rinitialiser la couleur du bouton

                }, 5000);
            } else {
                console.error('Erreur lors de l\'envoi de l\'adresse email au backend Flask.');
            }
        })
        .catch(error => {
            console.error('Erreur lors de l\'envoi de l\'adresse email au backend Flask : ', error);
        });
    }

    return (
        <div className='S4'>
            <section className="section4">
                <li className="section_section4">
                    <div className='S4_4'>
                        <h1>Notre projet vous intresse ?</h1>
                        <input className='S4_email' type="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)}/>
                        <button className="S4_button_signUp" id="submitBtn" onClick={handleSignIn}>Rester connect !</button>
                        <p>{message}</p> {/* Afficher le message en dessous du bouton */}

                    </div>
                </li>

                <li className="section_section4">
                    <h1>Des questions ?</h1>
                    <div className='S4_2'>
                            <div className='S4_3'> 
                                <h2>Contactez-nous</h2>
                                <p>+33 X XX XX XX XX<br/>
                                +33 X XX XX XX XX<br/>
                                startup.contact@gmail.com</p>
                            </div>

                            <div className='S4_3'>
                                <div>
                                    <h2>Suivez-nous</h2>
                                </div>
                                <div className="social-icons">
                                    <a href="https://www.linkedin.com/in/thi-nang-nathalie-pham/" target="_blank" rel="noopener noreferrer"><img src={Linkedin} alt="LinkedIn"/></a>
                                    <a href="https://www.facebook.com/etienne.girault" target="_blank" rel="noopener noreferrer"><img src={Facebook} alt="Facebook"/></a>
                                    <a href="https://www.instagram.com/votre-compte" target="_blank" rel="noopener noreferrer"><img src={Instagram} alt="Instagram"/></a>
                                    <a href="https://twitter.com/votre-compte" target="_blank" rel="noopener noreferrer"><img src={Twitter} alt="Twitter"/></a>
                                </div>
                            </div>

                    </div>
                </li>

            </section>
        </div>
    );
}

export default Section4;
