// AuthPopup.js

import React, { useState, useEffect, useRef } from 'react';
import './AuthPopup.css';
import imageSrc from '../background-image.jpg'; // Replace with the correct path to your image
import Linkedin from '../linkedin.png';
import Facebook from '../facebook.png';
import Instagram from '../instagram.png';
import Twitter from '../twitter.png';
import Art from '../art_inscription.png'
import Collection from '../collection_inscription.png'
import Startup from '../startup_inscription.png'
import Fund from '../pe_inscription.png'
import { Carousel } from 'antd';

const AuthPopup = ({ onClose }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const popupRef = useRef();
  const [message, setMessage] = useState('');

  const isValidEmail = (email) => {
    // Expression rgulire pour vrifier si l'e-mail est valide
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleSignIn = () => {
      if (!email.trim() || !isValidEmail(email)) {
          setMessage(<span className="email-error2">Veuillez entrer une adresse e-mail valide.</span>);
          return;
      }
      // Envoyer l'adresse e-mail au backend Flask
      fetch('https://stratup1.azurewebsites.net/submit_form', {
          method: 'POST',
          headers: {
              'Content-Type': 'application/json'
          },
          body: JSON.stringify({ email: email })
      })
      .then(response => {
          if (response.ok) {
              console.log('Adresse email envoye avec succs au backend Flask.');
              setEmail(''); // Rinitialiser le champ email aprs l'envoi russi
              // Changer le texte et la couleur du bouton pendant 5 secondes
              setMessage(<span className="email-message2">Crons ensemble un avenir financier sans contraintes !</span>);
              const button = document.getElementById('submitBtn');
              button.innerText = 'Inscription russie!';
              button.style.color = 'rgb(0, 67, 67)';

              setTimeout(() => {
                  setMessage('');
                  button.innerText = 'Rester connect !';
                  button.style.backgroundColor = ''; // Rinitialiser la couleur du bouton
                  button.style.color = '';
              }, 5000);
          } else {
              console.error('Erreur lors de l\'envoi de l\'adresse email au backend Flask.');
          }
      })
      .catch(error => {
          console.error('Erreur lors de l\'envoi de l\'adresse email au backend Flask : ', error);
      });
  }

  const handleClickOutside = (event) => {
    if (popupRef.current && popupRef.current.contains(event.target.querySelector('.popup-content'))) {
      onClose();
    }
  };

  useEffect(() => {
    document.addEventListener('click', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, [onClose]);

  return (
    <div className="auth-popup" ref={popupRef}>
      <div className="popup-content">
        <div className="form-container">
          <div className="second-container">
            <div className="form-item">
              <h1 className="form-title">Notre projet vous intresse ?</h1>
            </div>
            <div className="form-item">
              <input type="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
            </div>
            {/* <div className="form-item">
              <input type="password" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)} />
            </div> */}
            <div className="button-container">
              <button className="button_signUp" id="submitBtn" onClick={handleSignIn}>Rester connect !</button>
              <p>{message}</p> {/* Afficher le message en dessous du bouton */}

            </div>
            <div className="form-item">
              <h1 className="form-title">Suivez-nous !</h1>
            </div>
            <div class="social-icons">
                  <a href="https://www.linkedin.com/in/thi-nang-nathalie-pham/" target="_blank"><img src={Linkedin}></img></a>
                  <a href="https://www.facebook.com/etienne.girault" target="_blank"><img src={Facebook}></img></a>
                  <a href="https://www.instagram.com/votre-compte" target="_blank"><img src={Instagram}></img></a>
                  <a href="https://twitter.com/votre-compte" target="_blank"><img src={Twitter}></img></a>
              </div>
          </div>
        </div>

        <div className="image-slider">
          <div class="image_element">
            <div className="caroussel">
              <Carousel autoplay>
                <img class="image" src={Art}></img>
                <img class="image" src={Collection}></img>
                <img class="image" src={Startup}></img>
                <img class="image" src={Fund}></img>
              </Carousel>
            </div>
          </div>
        </div>

        <div className="close-button" onClick={onClose}>
          X
        </div>
      </div>
    </div>
  );
};

export default AuthPopup;
