// App.js
import React, { useState, useEffect } from 'react';
import './App.css';
import Header from './Header';
import Section1 from './Section1';
import Section2 from './Section2';
import Section3 from './Section3';
import Section4 from './Section4';
import Footer from './Footer';
import ScrollDetector from './ScrollDetector';

const App = () => {
  const [scrollPosition, setScrollPosition] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      setScrollPosition(window.scrollY);
    };

    // Scroll logic to transition between sections
    /* const handleSectionScroll = (event) => {
      if (event.deltaY > 0) {
        document.getElementById('section2').scrollIntoView({ behavior: 'smooth' });
      } else {
        document.getElementById('section1').scrollIntoView({ behavior: 'smooth' });
      }
    }; */

    window.addEventListener('scroll', handleScroll);
    //window.addEventListener('wheel', handleSectionScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
     // window.removeEventListener('wheel', handleSectionScroll);
    };
  }, []);

  // Fonction pour calculer l'opacit en fonction de la distance par rapport au centre
  const calculateOpacity = (distanceFromCenter) => {
    const maxDistance = window.innerHeight / 2; // Utilisez la moiti de la hauteur de l'cran comme rfrence
    const maxOpacity = 1; // Ajustez la valeur maximale d'opacit (0.8 pour 80% d'opacit)

    // Calculez l'opacit en fonction de la distance par rapport au centre
    let opacity = 1 - Math.abs(distanceFromCenter) / maxDistance;

    // Limitez l'opacit  la valeur maximale
    opacity = Math.min(opacity, maxOpacity);

    return opacity < 0 ? 0 : opacity; // Assurez-vous que l'opacit ne devienne pas ngative
  };

  return (
    <body>
      <Header />
      {/* Section 1 */}
      <section id='section1' style={{ opacity: 1}}>
        <Section1/>
      </section>
      {/* Section 3 */}
      <section id="section3" style={{ opacity: 1}}>
        <Section3/>
      </section>

      {/* Section 2 */}
      <section id="section2" style={{ opacity: 1}}>
          <Section2/>
      </section>

      {/* Section 4 */}
      <section id="section4" style={{ opacity: 1}}>
          <Section4/>
      </section>
      <Footer />
    </body>
  );
};

export default App;
