// Header.js
import React, { useState } from 'react';
import './App.css';
import { Link, useLocation } from 'react-router-dom';
import AuthPopup from './AuthentificationPopUp/AuthentificationPopUp';
import Logo from './Logo.png';


const Header = () => {
  const scrollToSection = (sectionId) => {
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };
  const [isPopupVisible, setPopupVisibility] = useState(false);

  const handleOpenPopup = () => {
  setPopupVisibility(true);
  };

  const handleClosePopup = () => {
  setPopupVisibility(false);
  };

  return (
    <header>
      <img className="logo" onClick={() => scrollToSection('section1')} src={Logo}/>
      <ul className='header-menu'>
        <li onClick={() => scrollToSection('section3')}>Comment a marche ?</li>
        <li onClick={() => scrollToSection('section2')}>Nos produits</li>
        <li onClick={() => scrollToSection('section4')}>Contact</li>
        <li className="button_openPopUp" onClick={handleOpenPopup}>Se connecter / S'inscrire</li>
          {isPopupVisible && <AuthPopup onClose={handleClosePopup} />}
      </ul>
    </header>
  );
};

export default Header;